.App {
  text-align: center;
}
body{
  background-color: #282c34;
}

.App-header {
  position: absolute;
  top:50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 250px;
  height: 250px;
  box-sizing: border-box;
  
}
.App-link {
  color: #61dafb;
}
.circle1{
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border-style: solid;
  border-width: 5px;
  border-color:  #f1c40f #e74c3c #9b59b6 #2ecc71;
  animation: animateCircle1 5s linear infinite;
}
.circle0{
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border-style: solid;
  border-width: 5px;
  border-color:  #f1c40f #e74c3c #9b59b6 #2ecc71;
  animation: animateCircle1 5s linear infinite;
  background: url('b.jpg');
  background-repeat: no-repeat;
  background-size: cover;
}
.circle2{
  position: absolute;
  top: 10px;
  left:10px;
  width: calc(100% - 10px);
  height: calc(100% - 10px);
  border-radius: 50%;
  background: url('a.jpg');
  background-repeat: no-repeat;
  background-size: cover;

  
}
.circle3{
  position: absolute;
  top: 10px;
  left:10px;
  width: calc(100% - 10px);
  height: calc(100% - 10px);
  border-radius: 50%;
  /* background: url('b.jpg'); */
  background-repeat: no-repeat;
  background-size: cover;

  
}

@keyframes animateCircle1 {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
